html {
  box-sizing: border-box;
  font-size: 16px;
  font-family: 'Lato', sans-serif;
  color: white; }

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

.container {
  width: 100%;
  max-width: 1100px;
  margin-right: auto;
  margin-left: auto;
  padding: 0 0.5rem; }

#header {
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../assets/img/elcapitan.jpg");
  background-position: center;
  background-size: cover;
  overflow: hidden; }
  #header--small {
    height: 10vh;
    border-bottom: 2px solid #F9C802; }

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0.5rem; }
  @media (min-width: 800px) {
    .nav {
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      padding-top: 1rem; } }
  .nav__logo {
    font-size: 2rem;
    margin-bottom: .5rem;
    font-family: 'Sacramento', cursive; }
    @media (min-width: 800px) {
      .nav__logo {
        font-size: 3rem;
        margin-bottom: 0; } }
  .nav__items {
    display: flex; }
  .nav__link {
    display: inline-block;
    text-decoration: none;
    text-transform: uppercase;
    color: white;
    font-size: 1rem;
    margin: 0 1rem;
    padding-bottom: 0.5rem;
    font-weight: 300;
    border-bottom: 2px solid transparent;
    transition: border-color .2s linear; }
    .nav__link:hover {
      border-color: white; }
    .nav__link.active {
      border-color: #F9C802; }
    @media (min-width: 800px) {
      .nav__link {
        font-size: 1.2rem; } }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

a {
  text-decoration: none;
  color: white;
  display: inline-block; }

.btn {
  background-color: white;
  color: black;
  padding: 1rem 1.5rem;
  font-size: 1rem;
  letter-spacing: 1px;
  transition: all .2s linear; }
  .btn:hover {
    background-color: #F9C802; }

#header .content {
  width: 100%;
  height: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center; }
  #header .content__header {
    font-size: 2rem;
    line-height: 1.8; }
    @media (min-width: 800px) {
      #header .content__header {
        font-size: 3rem;
        letter-spacing: 1px; } }
  #header .content__paragraph {
    font-size: 1.2rem;
    line-height: 1.8; }
    @media (min-width: 800px) {
      #header .content__paragraph {
        font-size: 1.6rem;
        letter-spacing: 1px; } }
